
* {
  outline: unset !important;
  text-decoration: none !important;
  direction: ltr;
}

body {
  padding: 0 !important;
  margin: 0 !important;
  overflow: hidden auto;
  width: 100%;
  max-height: 100vh;
  font-size: 16px;
}


.app {
  max-height: 100vh;
  overflow: hidden;
}

.header{
  height: 16vh;
  background-color: #fff;
}

.header .title{
  height: 100%;
  display: flex;
  align-items: center;
}

.header .title h1{
  font-size: 22px;
  color: #767676;
}


.main{
  background-color: #f5f5f5;
  height: calc(100vh - 16vh);
  
}

.main .inner{
  height: 100%;
  padding-top: 5vh ;
  padding-bottom: 5vh ;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.main .inner .list{
  margin-right: 1rem;
  padding: 0.25rem;
  background-color: #fff;
  border-radius: 6px;
  overflow: hidden;
  height: 100%;
  box-shadow: 0px 3px 6px 1px rgb(0 0 0 / 12%);
}
.main .inner .list .crypto-card{
  padding: 1rem 0.5rem;
  margin: 0.5rem 1rem;
  position: relative;
  display: flex;
  align-items: center;
}
.main .inner .list .crypto-card .star{
  height: 40px;
  width: 70px;
  cursor: pointer;

}
.main .inner .list .crypto-card .star img{
  width: auto;
  height: 100%;
}
.main .inner .list .crypto-card::after{
  content: '';
  height: 1px;
  width: 84%;
  background-color: #d3d3d3;
  position: absolute;
  top: 105%;
  left: 8%;
}
.main .inner .list .crypto-card:last-child::after{
  display: none;
}

.main .inner .list .crypto-card .info{
  width: 100%;
  cursor: pointer;
  color: #707070;
}
.main .inner .list .crypto-card .info h6{
  font-size: 15px;
  font-weight: 600;
}
.main .inner .list .crypto-card .info span{
  font-size: 14px;
}
.main .inner .chart{
  width: 100%;
  margin-left: 2rem;
  padding: 0.25rem;
  background-color: #fff;
  border-radius: 6px;
  overflow: hidden;
  height: 74vh;
  box-shadow: 0px 3px 6px 1px rgb(0 0 0 / 12%);
}